import React from 'react';
import SemanticDatepicker from 'react-semantic-ui-datepickers';
import 'react-semantic-ui-datepickers/dist/react-semantic-ui-datepickers.css';
import './form.css'

const Form = ({states, onStateChange, districts, onDistrictChange, onDateChange, handleInput, pincode, onFormSubmit}) => {
    return (
        <form onSubmit={onFormSubmit} className="ui form">
            <h4 className="ui dividing header">Vaccine Center Information</h4>
            <div className="two fields">
                <div className="field">
                    <label>State</label>
                    <select onChange={onStateChange} className="ui fluid dropdown">
                        <option value="">State</option>
                        {
                            states.map(state =>  
                                <option key={state.state_id} value={state.state_id}>{state.state_name}</option>
                            )
                        }
                    </select>
                </div>
                <div className="field">
                    <label>District</label>
                    <select onChange={onDistrictChange} className="ui fluid dropdown">
                        <option value="">District</option>
                        {
                            districts.map(district =>  
                                <option key={district.district_id} value={district.district_id}>{district.district_name}</option>
                            )
                        }
                    </select>
                </div>
            </div>
            <div class="ui horizontal divider">
                Or
            </div>
            <div className="two fields">
                <div className="field">
                    <label>PIN Code</label>
                    <input type="text" className="ui input" value={pincode} onChange={handleInput}/>
                </div>
            </div>
            <div className="two fields">
                <div className="field">
                    <label>Date</label>
                    <SemanticDatepicker onChange={onDateChange}/>
                </div>
            </div>
            <div className="two fields">
                <div className="field">
                    <button className="ui primary button">Fetch Details</button>
                </div>
            </div>
        </form>
    )
}

export default Form;